


@mixin screenSmallerThan1000px {
    @media screen and (max-width: 1000px) {
        @content;
    }
}
@mixin screenSmallerThan800px {
    @media screen and (max-width: 800px) {
        @content;
    }
}
@mixin screenSmallerThan570px {
    @media screen and (max-width: 570px) {
        @content;
    }
}


